<template>
  <el-dialog
    :title="title"
    :visible.sync="v"
    :before-close="handleClose"
    width="900px"
    :close-on-click-modal="false"
    append-to-body
    @open="open"
  >
    <el-form
      ref="paramFormRef"
      :model="paramForm"
      :rules="paramRules"
      label-width="246px"
      size="small"
      class="demo-ruleForm"
    >
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item :label="$t('strategyMag.strategyName')" prop="strategyName" :key="key">
            <el-input v-model="paramForm.strategyName" :placeholder="inputPh('strategyMag.strategyName')" :disabled="form.opt==='update'"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('strategyMag.equipmentSupplierId')" prop="equipmentSupplierId" :key="key">
            <ct-select
              :disabled="form.opt==='update'"
              v-model="paramForm.equipmentSupplierId"
              :placeholder="selectPh('strategyMag.equipmentSupplierId')"
              :options="equipmentSupplierIdOptions"
              :labelKey="'equipmentSupplierName'"
              :valueKey="'equipmentSupplierId'"
            />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('strategyMag.custId')" prop="custId" :key="key">
            <ct-select
              :disabled="form.opt==='update'"
              v-model="paramForm.custId"
              :placeholder="selectPh('strategyMag.custId')"
              :options="custOptions"
            >
            </ct-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" style="visibility: hidden">
          <el-form-item label="">
            <el-input></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('strategyMag.heartbeatReportingFrequency')" class="tip-box" prop="heartBeatSwitch">
            <el-tooltip class="item" effect="dark"
                        :content="$i18n.locale === 'zh'?'心跳上报频率30秒到86400秒，仅作为上报，连续两次上报未收到可表示下线':
                        'The heartbeat report frequency ranges from 30 seconds to 86400 seconds. If the heartbeat report is not received twice, it indicates that the heartbeat is offline'"
                        placement="top">
              <i class="el-icon-info"></i>
            </el-tooltip>
            <span class="radio-box">
              <el-radio v-model="paramForm.heartBeatSwitch" :label="'1'">{{$t('strategyMag.on')}}</el-radio>
              <el-radio v-model="paramForm.heartBeatSwitch" :label="'0'">{{$t('strategyMag.off')}}</el-radio>
            </span>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="`${$t('strategyMag.heartBeatIntervalDialog')}(s)`" prop="heartBeatInterval">
            <el-input type="number" v-model="paramForm.heartBeatInterval" :disabled="paramForm.heartBeatSwitch==='0'" :placeholder="inputPh('strategyMag.heartBeatIntervalDialog')"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('strategyMag.priorityNetworkSwitch')" class="tip-box" prop="priorityNetworkSwitch">
            <el-tooltip class="item" effect="dark"
                        :content="$i18n.locale === 'zh'?'设置设备上电时的默认运营商':'Set the default carrier for powering on the device'"
                        placement="top">
              <i class="el-icon-info"></i>
            </el-tooltip>
            <span class="radio-box">
              <el-radio v-model="paramForm.priorityNetworkSwitch" :label="'1'">{{$t('strategyMag.on')}}</el-radio>
              <el-radio v-model="paramForm.priorityNetworkSwitch" :label="'0'">{{$t('strategyMag.off')}}</el-radio>
            </span>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('strategyMag.priorityNetwork')" prop="priorityNetwork">
            <ct-select
              v-model="paramForm.priorityNetwork"
              :placeholder="inputPh('strategyMag.priorityNetwork')"
              :options="priorityNetworkOptions"
              :disabled="paramForm.priorityNetworkSwitch==='0'"
            />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('strategyMag.autoCardCut')" class="tip-box" prop="checkCardMode">
            <span class="radio-box">
              <el-radio v-model="paramForm.checkCardMode" :label="'1'">{{$t('strategyMag.on')}}</el-radio>
              <el-radio v-model="paramForm.checkCardMode" :label="'0'">{{$t('strategyMag.off')}}</el-radio>
            </span>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            &nbsp;&nbsp;&nbsp;
          </el-form-item>
        </el-col>
        <template v-if="paramForm.checkCardMode === '1'">
          <el-col :span="12">
            <el-form-item :label="$t('strategyMag.weakSignalSwitch')" class="tip-box" prop="weakSignalSwitch">
              <el-tooltip class="item" effect="dark"
                          :content="$i18n.locale === 'zh'?'当前信号值低于某值时切换':'Switch when the current signal value is below a certain value'"
                          placement="top">
                <i class="el-icon-info"></i>
              </el-tooltip>
              <span class="radio-box">
                <el-radio v-model="paramForm.weakSignalSwitch" :label="'1'">{{$t('strategyMag.on')}}</el-radio>
                <el-radio v-model="paramForm.weakSignalSwitch" :label="'0'">{{$t('strategyMag.off')}}</el-radio>
              </span>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="`${$t('strategyMag.weakSignalValueDialog')}(-dBm)`" prop="weakSignalValue">
              <el-input type="number" v-model="paramForm.weakSignalValue" :disabled="paramForm.weakSignalSwitch==='0'" :placeholder="inputPh('strategyMag.weakSignalValueDialog')"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="$t('strategyMag.networkSwitch')" class="tip-box" prop="networkSwitch">
              <el-tooltip class="item" effect="dark"
                          :content="$i18n.locale === 'zh'?'2G切换；3G切换':'2Gswitching；3Gswitching'"
                          placement="top">
                <i class="el-icon-info"></i>
              </el-tooltip>
              <span class="radio-box">
                <el-radio v-model="paramForm.networkSwitch" :label="'1'">{{$t('strategyMag.on')}}</el-radio>
                <el-radio v-model="paramForm.networkSwitch" :label="'0'">{{$t('strategyMag.off')}}</el-radio>
              </span>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="$t('strategyMag.networkType')" prop="networkType">
              <ct-select
                v-model="paramForm.networkType"
                :placeholder="inputPh('strategyMag.networkType')"
                :options="networkTypeOptions"
                :disabled="paramForm.networkSwitch==='0'"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="$t('strategyMag.bestSignalStatus')" class="tip-box" prop="bestSignalStatus">
              <el-tooltip class="item" effect="dark"
                          :content="$i18n.locale === 'zh'?'全能卡在每次上电时，会先监测所有运营商连接质量，选取最优连接，需要一定初始化时间':
                          'Each time the universal card is powered on, it monitors the connection quality of all carriers and selects the optimal connection, which requires a certain initialization time'"
                          placement="top">
                <i class="el-icon-info"></i>
              </el-tooltip>
              <span class="radio-box">
                <el-radio v-model="paramForm.bestSignalStatus" :label="'1'">{{$t('strategyMag.on')}}</el-radio>
                <el-radio v-model="paramForm.bestSignalStatus" :label="'0'">{{$t('strategyMag.off')}}</el-radio>
              </span>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              &nbsp;&nbsp;&nbsp;
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="$t('strategyMag.falseSignalSwitch')" class="tip-box" prop="falseSignalSwitch">
              <el-tooltip class="item" effect="dark"
                          :content="$i18n.locale === 'zh'? '检测网址/IP，可配置两个或一个，如果2个(以英文逗号隔开)，都不通则触发切换，如果是1个，需要连续两次不通才触发切换，检测间隔一到，上述检测都应连续进行检测间隔':
                          'If two urls /IP addresses (separated by commas (,)) are not common, switchover is triggered. If two urls /IP addresses are not common, switchover is triggered only when two urls /IP addresses are not common. If two urls /IP addresses are not common, the detection interval is once'"
                          placement="top">
                <i class="el-icon-info"></i>
              </el-tooltip>
              <span class="radio-box">
                <el-radio v-model="paramForm.falseSignalSwitch" :label="'1'">{{$t('strategyMag.on')}}</el-radio>
                <el-radio v-model="paramForm.falseSignalSwitch" :label="'0'">{{$t('strategyMag.off')}}</el-radio>
              </span>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="$t('strategyMag.falseSignalIpsDialog')" prop="falseSignalIps">
              <el-input v-model="paramForm.falseSignalIps" :disabled="paramForm.falseSignalSwitch==='0'" :placeholder="inputPh('strategyMag.falseSignalIpsDialog')"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="`${$t('strategyMag.falseSignalIntervalDialog')}(s)`" prop="falseSignalInterval">
              <el-input v-model="paramForm.falseSignalInterval" :disabled="paramForm.falseSignalSwitch==='0'" :placeholder="inputPh('strategyMag.falseSignalIntervalDialog')"></el-input>
            </el-form-item>
          </el-col>
        </template>
      </el-row>
    </el-form>
    <span slot="footer" class="dialog-footer">
<!--        <el-button type="primary" size="small" :loading="loading" @click="clickOk">确 定</el-button>-->
        <el-button size="small" @click="cancel">{{$t('strategyMag.cancel')}}</el-button>
      </span>
  </el-dialog>
</template>

<script>
import {mapGetters} from 'vuex'
import langMixin from '@/mixins/langMixin'
export default {
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    form:{
      type: Object,
      default:()=>{
        return {
          opt:'add',
          data:{}
        }
      }
    },
    custOptions:{
      type: Array,
      default(){
        return []
      }
    }
  },
  data() {
    return {
      title:'',
      defaultParamForm: {
        strategyName :'',
        equipmentSupplierId: '',
        custId:'',
        heartBeatSwitch:'1',
        heartBeatInterval:null,
        checkCardMode:'1',
        weakSignalSwitch:'1',
        weakSignalValue:null,
        networkSwitch:'1',
        networkType:'',
        bestSignalStatus:'1',
        priorityNetworkSwitch:'1',
        priorityNetwork:'',
        falseSignalSwitch:'1',
        falseSignalIps:'',
        falseSignalInterval:null
      },
      paramForm:{},
      equipmentSupplierIdOptions:[],
      networkTypeOptions:[
        {label:'2G',value:'1'},
        {label:'3G',value:'2'},
        {label:'2/3G',value:'3'},
      ],
      zhPriorityNetworkOptions:[
        {label:'中国电信',value:'1'},
        {label:'中国移动',value:'2'},
        {label:'中国联通',value:'3'},
      ],
      enPriorityNetworkOptions:[
        {label:'China Telecom',value:'1'},
        {label:'China Mobile',value:'2'},
        {label:'China Unicom',value:'3'},
      ],
      loading:false,
      paramRules: {
        strategyName: [
          { required: true, message: this.inputPh('strategyMag.strategyName'), trigger: 'blur' },
          { validator: this.validatestrategyName, trigger: 'blur' }
        ],
        equipmentSupplierId:[
          { required: true, message: this.inputPh('strategyMag.equipmentSupplierId'), trigger: 'change' },
        ],
        custId:[
          { required: true, message: this.inputPh('strategyMag.custId'), trigger: 'change' },
        ],
      },
      key:0
    }
  },
  computed: {
    ...mapGetters('user', ['userInfo']),
    priorityNetworkOptions(){
      return this.$i18n.locale === 'zh' ? this.zhPriorityNetworkOptions:this.enPriorityNetworkOptions
    },
    v: {
      get: function() {
        return this.visible
      },
      set: function(value) {
        this.$emit('update:visible', value)
      }
    }
  },
  created() {
    this.queryEquipmentSupplierId()
  },
  methods: {
    open(){
      this.key++
      this.paramForm = JSON.parse(JSON.stringify(this.defaultParamForm))
      if(this.form.opt === 'add'){
        this.title = this.$t('strategyMag.add')
      }
      if(this.form.opt === 'update'){
        this.title = this.$t('strategyMag.update')
        if(this.form.data.strategyId){
          for (let key in this.paramForm){
            this.paramForm[key] = this.form.data[key]
          }
          this.paramForm.strategyId = this.form.data.strategyId
          if(this.paramForm.weakSignalValue){
            this.paramForm.weakSignalValue *= -1
          }
        }
      }
    },
    queryEquipmentSupplierId(){
      this.$api.fullStrategyMag.queryEquipmentSupplierId().then(res=>{
        this.equipmentSupplierIdOptions = res
      })
    },
    clickOk() {
      if(this.paramForm.heartBeatSwitch ==='1'){//心跳上报频率开启
        if(!this.paramForm.heartBeatInterval){
          this.$i18n.locale === 'zh' ? this.$message.error(`心跳上报频率开启时心跳上报间隔必填`):
            this.$message.error(`Heartbeat Report frequency Heartbeat report interval When enabled This parameter is mandatory`)
          return
        }
        if(this.paramForm.heartBeatInterval < 30 || this.paramForm.heartBeatInterval > 86400){
          this.$i18n.locale === 'zh' ? this.$message.error(`心跳上报间隔必须在30至86400秒之间`):
            this.$message.error(`Heartbeat reporting intervals must be between 30 and 86400 seconds`)
          return
        }
      }else{//心跳上报频率关闭
        if(this.paramForm.heartBeatInterval){
          if(this.paramForm.heartBeatInterval < 30 || this.paramForm.heartBeatInterval > 86400){
            this.$i18n.locale === 'zh' ? this.$message.error(`心跳上报间隔必须在30至86400秒之间`):
              this.$message.error(`Heartbeat reporting intervals must be between 30 and 86400 seconds`)
            return
          }
        }
      }
      if(this.paramForm.weakSignalSwitch === '1'){//弱信号切换策略开启
        if(!this.paramForm.weakSignalValue){
          this.$i18n.locale === 'zh' ? this.$message.error(`弱信号切换策略开启时弱信号阈值必填`):
            this.$message.error(`The weak-signal threshold is mandatory when the weak-signal switching policy is enabled`)
          return
        }
        if(this.paramForm.weakSignalValue < 70 || this.paramForm.weakSignalValue > 113){
          this.$i18n.locale === 'zh' ? this.$message.error(`弱信号阈值范围-113至-70dBm`):
            this.$message.error(`The weak signal threshold ranges from -113 to -70 DBM`)
          return
        }
      }else{//弱信号切换策略关闭
        if(this.paramForm.weakSignalValue){
          if(this.paramForm.weakSignalValue < 70 || this.paramForm.weakSignalValue > 113){
            this.$i18n.locale === 'zh' ? this.$message.error(`弱信号阈值范围-113至-70dBm`):
              this.$message.error(`The weak signal threshold ranges from -113 to -70 DBM`)
            return
          }
        }
      }
      if(this.paramForm.networkSwitch === '1' && !this.paramForm.networkType){
        this.$i18n.locale === 'zh' ? this.$message.error(`网络制式切换策略开启时选择网络制式必填`):
          this.$message.error(`This parameter is mandatory when selecting a network Mode switchover policy is enabled`)
        return
      }
      if(this.paramForm.priorityNetworkSwitch === '1' && !this.paramForm.priorityNetwork){
        this.$i18n.locale === 'zh' ? this.$message.error(`优先卡配置策略开启时选择优先使用运营商必填`):
          this.$message.error(`Priority Card configuration policy Select Priority Carrier This parameter is mandatory`)
        return
      }
      if(this.paramForm.falseSignalSwitch === '1'){
        if(!this.paramForm.falseSignalIps){
          this.$i18n.locale === 'zh' ? this.$message.error(`假信号切换策略开启时指定检测网址/IP必填`):
            this.$message.error(`This parameter is mandatory when the false signal switchover policy is enabled`)
          return
        }
        if(!this.paramForm.falseSignalInterval){
          this.$i18n.locale === 'zh' ? this.$message.error(`假信号切换策略开启时检测间隔必填`):
            this.$message.error(`The detection interval is mandatory when the false signal switching policy is enabled`)
          return
        }
        if(this.paramForm.falseSignalInterval<= 0){
          this.$i18n.locale === 'zh' ? this.$message.error(`检测间隔大于0`):
            this.$message.error(`The detection interval is greater than 0`)
          return
        }
      }else{
        if(this.paramForm.falseSignalInterval && this.paramForm.falseSignalInterval<= 0){
          this.$i18n.locale === 'zh' ? this.$message.error(`检测间隔大于0`):
            this.$message.error(`The detection interval is greater than 0`)
          return
        }
      }
      !this.paramForm.createStaff && (this.paramForm.createStaff = this.userInfo.userId)
      !this.paramForm.updateStaff && (this.paramForm.updateStaff = this.userInfo.userId)
      if(this.paramForm.weakSignalValue > 0){
        this.paramForm.weakSignalValue *= -1
      }
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        this.loading = true
        this.$api.fullStrategyMag[this.form.opt === 'add'?'saveStrategy':'editStrategy'](this.paramForm).then(res=>{
          this.$refs.paramFormRef.resetFields()
          this.$message.success(`${this.form.opt === 'add'?'新增':'修改'}策略成功！`)
          this.v = false
          this.loading = false
          this.$emit('query', true)
        }).catch((err) => {
          this.loading = false
          //如果报错则展示正数
          if(this.paramForm.weakSignalValue < 0){
            this.paramForm.weakSignalValue *= -1
          }
        })
      })
    },
    cancel() {
      this.v = false
    },
    handleClose() {
      this.cancel()
    },
    validatestrategyName(rule, value, callback) {
      const regex1 = /^[\u4e00-\u9fa5A-Za-z@#$%^&*]+$/;
      const regex2 = /^.{1,20}$/;
      if (!regex1.test(value)) {
        this.$i18n.locale === 'zh' ? callback(new Error('策略名称只支持中文、大写字母、小写字母和特殊字符@、#、$、%、^、&、*')):
          callback(new Error('The policy name can contain only lowercase and uppercase letters, and special characters such as @, #, $, %, ^, &, and *'))
        return
      }
      if (!regex2.test(value)) {
        this.$i18n.locale === 'zh' ? callback(new Error('策略名称不能超过20个字')):
          callback(new Error('The policy name cannot exceed 20 characters'))
        return
      }
      callback()
    },
  },
  watch:{
    'paramForm.heartBeatSwitch':{
      handler(val){
        if(val === '0'){
          this.paramForm.heartBeatInterval = null
        }
      }
    },
    'paramForm.priorityNetworkSwitch':{
      handler(val){
        if(val === '0'){
          this.paramForm.priorityNetwork = ''
        }
      }
    },
    'paramForm.weakSignalSwitch':{
      handler(val){
        if(val === '0'){
          this.paramForm.weakSignalValue = null
        }
      }
    },
    'paramForm.networkSwitch':{
      handler(val){
        if(val === '0'){
          this.paramForm.networkType = ''
        }
      }
    },
    'paramForm.falseSignalSwitch':{
      handler(val){
        if(val === '0'){
          this.paramForm.falseSignalIps = ''
          this.paramForm.falseSignalInterval = null
        }
      }
    },
  }
}
</script>
<style lang="scss" scoped>
  .tip-box{
    position: relative;
    i{
      position: absolute;
      left: -10px;
      top: 9px;
    }
    .radio-box{
      padding-left: 20px;
    }
  }
</style>
<style scoped>
  ::v-deep .el-form-item--small.el-form-item{
    margin-bottom: 24px !important;
    display: flex;
  }
  ::v-deep .el-form-item .el-form-item__content {
    margin-left: 0px !important;
    flex: 1;
  }
</style>
