<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <div class="row-container">
          <div class="left-row">
            <el-row :gutter="20">
              <el-col :span="6">
                <el-form-item :label="$t('strategyMag.strategyId')" prop="strategyId">
                  <el-input
                    @input="value => qryParam.strategyId = value.replace(/\D/g, '')"
                    v-model="qryParam.strategyId"
                    :placeholder="inputPh('strategyMag.strategyId')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('strategyMag.strategyName')" prop="strategyName">
                  <el-input
                    v-model="qryParam.strategyName"
                    :placeholder="inputPh('strategyMag.strategyName')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('strategyMag.equipmentSupplierId')"  prop="equipmentSupplierId">
                  <ct-select
                    v-model="qryParam.equipmentSupplierId"
                    :placeholder="selectPh('strategyMag.equipmentSupplierId')"
                    :options="equipmentSupplierIdOptions"
                    :labelKey="'equipmentSupplierName'"
                    :valueKey="'equipmentSupplierId'"
                  >
                  </ct-select>
                </el-form-item>
              </el-col>
              <div v-show="powerSearch" class="more-query-group">
                <el-col :span="6">
                  <el-form-item :label="$t('strategyMag.custId')" prop="custId">
                    <ct-select
                      v-model="qryParam.custId"
                      :placeholder="selectPh('strategyMag.custId')"
                      :options="custOptions"
                    >
                    </ct-select>
                  </el-form-item>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="right-row">
            <el-col :span="6">
              <el-form-item label-width="0">
                <div class="search-button-group">
                  <el-button
                    type="primary"
                    icon="el-icon-search"
                    @click="querySimCardListForPage(false)"
                  >{{ $t('strategyMag.query') }}
                  </el-button
                  >
                  <el-button icon="el-icon-refresh" @click="reset">{{
                      $t('strategyMag.resetting')
                    }}</el-button>
                  <show-more-button v-model="powerSearch" />
                </div>
              </el-form-item>
            </el-col>
          </div>
        </div>
        <div style="display: flex;justify-content: space-between;">
          <el-form-item label-width="0">
            <el-button type="primary" icon="el-icon-plus" @click="add">
              {{ $t('strategyMag.add') }}
            </el-button>
          </el-form-item>
          <el-form-item label-width="0">
<!--            <el-button icon="el-icon-download" @click="exportTable">-->
<!--              导出数据-->
<!--            </el-button>-->
          </el-form-item>
        </div>
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        height="100%"
        width="100%"
        :element-loading-text="$t('strategyMag.loading')"
        :data="tableData"
        :border="true"
      >
        <el-table-column
          prop="strategyId"
          :label="$t('strategyMag.strategyId')"
          show-overflow-tooltip
          resizable
          min-width="110">
        </el-table-column>
        <el-table-column
          prop="equipmentSupplierIdDesc"
          :label="$t('strategyMag.equipmentSupplierId')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="strategyName"
          :label="$t('strategyMag.strategyName')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="checkCardModeDesc"
          :label="$t('strategyMag.checkCardModeDesc')"
          show-overflow-tooltip
          min-width="110"
        >
          <template slot-scope="scope">
            {{OnAndOff(scope.row.checkCardMode)}}
          </template>
        </el-table-column>
        <el-table-column
          prop="heartBeatInterval"
          :label="$t('strategyMag.heartBeatInterval')"
          show-overflow-tooltip
          min-width="150"
        >
        </el-table-column>
        <el-table-column
          prop="weakSignalValue"
          :label="$t('strategyMag.weakSignalValue')"
          show-overflow-tooltip
          min-width="150"
        ></el-table-column>
        <el-table-column
          prop="networkSwitchDesc"
          :label="$t('strategyMag.networkSwitchDesc')"
          show-overflow-tooltip
          min-width="110"
        >
          <template slot-scope="scope">
            {{OnAndOff(scope.row.networkSwitch)}}
          </template>
        </el-table-column>
        <el-table-column
          prop="bestSignalStatusDesc"
          :label="$t('strategyMag.bestSignalStatusDesc')"
          show-overflow-tooltip
          min-width="110"
        >
          <template slot-scope="scope">
            {{OnAndOff(scope.row.bestSignalStatus)}}
          </template>
        </el-table-column>
        <el-table-column
          prop="priorityNetworkSwitchDesc"
          :label="$t('strategyMag.priorityNetworkSwitchDesc')"
          show-overflow-tooltip
          min-width="110"
        >
          <template slot-scope="scope">
            {{OnAndOff(scope.row.priorityNetworkSwitch)}}
          </template>
        </el-table-column>
        <el-table-column
          prop="falseSignalIps"
          :label="$t('strategyMag.falseSignalIps')"
          show-overflow-tooltip
          min-width="140"
        ></el-table-column>
        <el-table-column
          prop="falseSignalInterval"
          :label="$t('strategyMag.falseSignalInterval')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column :label="$t('strategyMag.operate')" fixed="right" min-width="80">
          <template slot-scope="{ row }">
            <div class="action-button-group">
              <el-button type="text" @click="update(row)">{{$t('strategyMag.update')}}</el-button>
<!--              <el-button type="text" v-comp-priv="96" style="color: #FB522B" @click="del(row)">删除</el-button>-->
            </div>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <AddDialog
      :visible.sync="form.visible"
      :form="form"
      :custOptions="custOptions"
      @query="querySimCardListForPage"
    >
    </AddDialog>
  </div>
</template>
<script>
import fetch from '@/request/fetch'
import AddDialog from '@/views/infoQuery/strategyMag/addDialog.vue'
import langMixin from '@/mixins/langMixin'
const defaultForm = {
  strategyId: '',
  strategyName: '',
  equipmentSupplierId: '',
  custId: ''
}
export default {
  components: { AddDialog },
  mixins: [langMixin],
  data() {
    return {
      powerSearch: false,
      loading: false,
      form: {
        visible: false,
        opt: '',
        data: {}
      },
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0
      },
      tableData: [],
      qryParam: { ...defaultForm },
      equipmentSupplierIdOptions: [],
      zhCustOptions: [
        {
          label: '中国电信',
          value: '1',
          children: [
            { label: '北京电信', value: '1-1' },
            { label: '南京电信', value: '1-2' },
          ],
        },
        {
          label: '中国移动',
          value: '2',
          children: [
            { label: '上海移动', value: '2-1' },
            { label: '武汉移动', value: '2-2' },
            { label: '天津移动', value: '2-3' },
          ],
        },
        {
          label: '中国联通',
          value: '3',
          children: [
            { label: '南昌联通', value: '3-1' },
            { label: '长沙联通', value: '3-2' },
          ],
        },
      ],
      enCustOptions: [
        {
          label: 'China Telecom',
          value: '1',
          children: [
            { label: 'Beijing Telecom', value: '1-1' },
            { label: 'Nanjing Telecom', value: '1-2' },
          ],
        },
        {
          label: 'China Mobile',
          value: '2',
          children: [
            { label: 'Shanghai Mobile', value: '2-1' },
            { label: 'Wuhan Mobile', value: '2-2' },
            { label: 'Tianjin Mobile', value: '2-3' },
          ],
        },
        {
          label: 'China Unicom',
          value: '3',
          children: [
            { label: 'Nanchang Unicom', value: '3-1' },
            { label: 'Changsha Unicom', value: '3-2' },
          ],
        },
      ],
    }
  },
  created() {
    this.queryEquipmentSupplierId()
    this.querySimCardListForPage(false)
  },
  computed: {
    custOptions() {
      return [
        {label:'上海农机',value:'1'},
        {label:'贵州人保',value:'2'},
        {label:'南网科技',value:'3'},
      ]
      // return this.$i18n.locale === 'zh' ? this.zhCustOptions : this.enCustOptions
    },
    OnAndOff(){
      return (opt)=>{
        if(opt==='0'){
          return this.$i18n.locale === 'zh' ? '关闭':'Off'
        }
        if(opt==='1'){
          return this.$i18n.locale === 'zh' ? '开启':'On'
        }
        return '-'
      }
    }
  },
  methods: {
    queryEquipmentSupplierId() {
      this.$api.fullStrategyMag.queryEquipmentSupplierId().then(res => {
        this.equipmentSupplierIdOptions = res
      })
    },
    // 查询业务配置
    async querySimCardListForPage(pageChangeFlag = false) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const params = { ...this.qryParam, ...this.page }
      this.loading = true
      this.$api.fullStrategyMag.queryStrategyPage(params).then(res => {
        this.tableData = res.list || []
        this.page.total = res.total
      }).finally(() => {
        this.loading = false
      })
    },
    add() {
      this.form.visible = true
      this.form.opt = 'add'
    },
    update(row) {
      this.form.visible = true
      this.form.opt = 'update'
      this.form.data = row
    },
    del(row) {
      this.$confirm('请确认是否删除此策略，策略删除否不可恢复', '删除策略', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$api.fullStrategyMag.deleteStrategy({
          strategyId: row.strategyId,
          custId: row.custId,
          equipmentSupplierId: row.equipmentSupplierId,
          strategyName: row.strategyName
        }).then(res => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.querySimCardListForPage(false)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.querySimCardListForPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.querySimCardListForPage(true)
    },
    // 重置
    reset() {
      this.qryParam = { ...defaultForm }
      this.querySimCardListForPage(false)
    },
    // 导出
    exportTable() {
      const params = { ...this.qryParam, ...this.page }
      this.$api.fullStrategyMag.batchExportStrategy(params).then(response => {
        const self = this
        const h = this.$createElement
        this.$msgbox({
          title: '操作成功',
          type: 'success',
          center: true,
          message: h('p', { class: 'fs12' }, [
            '文件下载任务已生成，请到下载任务页面',
            h(
              'a',
              { class: 'cor-blue', on: { click: self.viewProgress } },
              '查看任务进度'
            )
          ]),
          showCancelButton: false,
          confirmButtonText: '知道了'
        }).then(() => {
        }).catch(() => {
        })
      })
    },
    viewProgress() {
      this.$msgbox.close()
      this.$router.push('/userMag/download')
    }
  }
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
</style>
